<template>
  <Codemirror
    v-model:value="code"
    :options="cmOptions"
    border
    placeholder=" placeholder"
    text-align="left"
    :height="200"
    @change="onChange"
    style="text-align: left"
  />
</template>

<script>
import Codemirror from "codemirror-editor-vue3";
// language
import "codemirror/mode/javascript/javascript.js";
import "codemirror/mode/python/python.js";


import { ref } from "vue";
export default {
  name: "MyCodemirror",
  props: {
    msg: String,
  },
  components: { Codemirror },
  setup() {
    const code = ref();

    return {
        
      code,
      cmOptions: {
        mode: "python", // 
        theme: "default", // 
        lineNumbers: true, // 
        smartIndent: true, // 
        indentUnit: 2, // 4
        foldGutter: true, // 
        styleActiveLine: true, // 
        completeSingle: false,
        lineWrapping: true,
      },
      onChange() {
        console.log(code);
      },
    };
  },
};
</script>